  border-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
}
.play__img--rotate {
  animation: rotate 20s linear infinite;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
